import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Button as MaterialButton,
} from "@material-ui/core";
import { DataGrid } from "@material-ui/data-grid";
import { Fragment, useEffect, useState } from "react";
import { useAlert } from "react-alert";
import { MdDelete, MdEdit } from "react-icons/md";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import {
    clearErrors,
  deleteOrder,
  getAllOrders,
} from "../../../../redux/actions/orderAction";
import { DELETE_ORDER_RESET } from "../../../../redux/constants/orderConstant";

import { adminAuth, userAuth } from "../../../../util/userAuth";
import Loader from "../../../layout/Loader/Loader";
import MetaData from "../../../layout/MetaData/MetaData";
import SideBar from "../Dashboard/SideBar/SideBar";
import "./Orders.css";

const Orders = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const alert = useAlert();

  const { isAuthenticated, user } = useSelector((state) => state.user);
  const { error, orders } = useSelector((state) => state.allOrders);
  const {
    error: orderDeleteError,
    isDeleted,
    loading,
  } = useSelector((state) => state.order);

  const [open, setOpen] = useState(false);

  const orderProductToggle = () => {
    setOpen((old) => (old ? false : true));
  };

  const orderDeleteHandler = (id) => {
    dispatch(deleteOrder(id));
    setOpen(false);
  };

  const columns = [
    {
      field: "id",
      headerName: "Order ID",
      flex: 0.5,
      minWidth:180
    },
    {
      field: "status",
      headerName: "Status",
      flex: 0.2,
      minWidth:130,
      cellClassName: (params) => {
        return params.getValue(params.id, "status") === "Delivered"
          ? "greenColor"
          : "redColor";
      },
    },
    {
      field: "noOfItems",
      headerName: "No of Items",
      type: "number",
      minWidth:130,
      flex: 0.2,
    },
    {
      field: "amount",
      headerName: "Amount",
      type: "number",
      minWidth:130,
      flex: 0.2,
    },
    {
      field: "actions",
      headerName: "Actions",
      flex: 0.2,
      minWidth:130,
      type: "number",
      sortable: false,
      renderCell: (params) => {
        return (
          <Fragment>
            <Link to={`/admin/order/${params.getValue(params.id, "id")}`}>
              <MdEdit />
            </Link>

            <Button onClick={orderProductToggle}>
              <MdDelete />
            </Button>
            <Dialog
              aria-labelledby="simple-dialog-title"
              open={open}
              onClose={orderProductToggle}
            >
              <DialogTitle>Delete Order</DialogTitle>
              <DialogContent className="submitDailog">
                Do you want to Delete this Order{" "}
                {params.getValue(params.id, "name")}
              </DialogContent>

              <DialogActions>
                <MaterialButton onClick={orderProductToggle} color="primary">
                  Cancel
                </MaterialButton>
                <MaterialButton
                  onClick={() =>
                    orderDeleteHandler(params.getValue(params.id, "id"))
                  }
                  color="secondary"
                >
                  Delete
                </MaterialButton>
              </DialogActions>
            </Dialog>
          </Fragment>
        );
      },
    },
  ];
  const rows = [];
  orders &&
    orders.forEach((order) => {
      rows.push({
        id: order._id,
        noOfItems: order.orderItems.length,
        status: order.orderStatus,
        amount: order.totalPrice,
      });
    });

  useEffect(() => {
    userAuth(isAuthenticated, navigate);
    adminAuth(true, user, navigate, alert);

      if (error) {
        alert.error(error);
        dispatch(clearErrors());
      }
      if (orderDeleteError) {
        alert.error(orderDeleteError);
        dispatch(clearErrors());
      }

      if (isDeleted) {
        alert.success("Order Deleted Successfully");
        dispatch({ type: DELETE_ORDER_RESET });
      }

    dispatch(getAllOrders());
  }, [
    isAuthenticated,
    navigate,
    alert,
    user,
    dispatch,
    error,
    orderDeleteError,
    isDeleted,
  ]);

  return (
    <Fragment>
      <MetaData title="All Orders - Admin" />

      <div className="dashboard">
        <SideBar />

        <div className="productListContainer">
          <h1 id="productListHeading">All Orders</h1>

          {loading ? (
            <Loader />
          ) : (
            <DataGrid
              columns={columns}
              rows={rows}
              pageSize={10}
              disableSelectionOnClick
              className="productListTable"
              autoHeight
            />
          )}
        </div>
      </div>
    </Fragment>
  );
};

export default Orders;
